  components: {
    GridProducts: ProductsGrid
  },
  computed: {
    ...mapState("products/favorites", ["favorites"])
  },
  mounted() {
    this.reloadData();
  },
  data() {
    return {};
  },
  methods: {
    ...mapActions("products/favorites", ["getFavorites"]),
    reloadData() {
        this.getFavorites();
    }
  }
};
</script>

